.scrollup {
  position: fixed;
  right: 2.5rem;
  bottom: -20%;
  background-color: var(--title-color);
  opacity: 0.8;
  padding: 0.3rem 0.5rem;
  border-radius: 0.4rem;
  z-index: var(--z-tooltip);
  transition: 0.3s;
}
.scrollup__icon {
  font-size: 1.5rem;
  color: var(--container-color);
}
.scrollup:hover {
  background-color: var(--title-color-dark);
  transform: translateY(-0.5rem);
}
.show-scroll {
  bottom: 3rem;
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
  .scrollup {
    right: 1.5rem;
    padding: 0.25rem 0.4rem;
  }
  .scrollup__icon {
    font-size: 1.25rem;
  }
}

/* For medium devices */

@media screen and (max-width: 576px) {
  .scrollup {
    right: 0.75rem;
    bottom: 4rem;
    padding: 0.25rem 0.4rem;
  }
  .scrollup__icon {
    font-size: 1rem;
  }
}
