.footer {  background-color: var(--container-color);
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}
.footer__container {
  padding: 2rem 0 6rem;
}
.footer__title,
.footer__link {
  color: var(-title-color);
}
.footer__title {
  text-align: center;
  margin-bottom: var(--mb-2);
}
.footer__link {
  color: var(--title-color-dark);
}
.footer__list {
  display: flex;
  justify-content: center;
  column-gap: 1.5rem;
  margin-bottom: var(--mb-2);
}
.footer__socials {
  display: flex;
  justify-content: center;
  column-gap: 1.25rem;
}
.footer__social-link {
  background-color: var(--title-color);
  color: var(--container-color);
  font-size: 1.25rem;
  padding: 0.4rem;
  border-radius: 0.5rem;
  display: inline-flex;
  transition: 0.3s;
}
.footer__social-link:hover {
  background-color: var(--title-color-dark);
}

.footer__copy {
  display: block;
  margin-top: 4.5rem;
  color: var(--title-color);
  text-align: center;
  font-size: var(--smaller-font-size);
}
.footer__brand {
  margin-top: 1.5rem;
  display: block;

  color: var(--title-color);
  text-align: center;
  font-size: var(--smaller-font-size);
}
.footer__brand a {
  color: var(--title-color);
  transition: 0.3s;
}
.footer__brand a:hover {
  color: var(--title-color-dark);
  font-weight: var(--font-medium);
}
.footer__brand a:hover .arrow {
  transform: translateX(0.12rem);
}
.brand__icon {
  padding-right: 0.5rem;
  font-size: 1rem;
}
.brand__name {
  color: var(--title-color-dark);
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
  .footer__social-link {
    padding: 0.25rem;
    border-radius: 0.25rem;
    font-size: 1rem;
  }
}
